import { defineStore } from 'pinia';
export const useDropdownStore = defineStore({
  // id is the name of the store
  // it is used in devtools and allows restoring state
  id: 'dropdown',
  state: () => ({
    commandName: '',
    uid: 0
  }),
  getters: {},
  actions: {
    setCommand(name) {
      this.commandName = name;
    },
    setUid() {
      this.uid = Date.now();
      return this.uid;
    }
  }
});